#Body {
  background-color: white;
  > .inner {
    @include inner;
    padding: 2rem 3rem 4rem;
    font-size: 1.4rem;
    line-height: 1.4; } }


@media screen and (max-width: $WIDTH_SP) {
  #Body {
    > .inner {
      @include inner-sp;
      padding-left: $PADDING_SP_BASE;
      padding-right: $PADDING_SP_BASE; } } }
