.page-title {
  padding: 1.4rem 0;
  > .title {
    margin-bottom: 0.6rem;
    font-size: 3.2rem;
    line-height: 1.2;
    font-weight: lighter; }
  > .breadcrumb {
    font-size: 1.2rem;
    line-height: 1;
    padding: 0.4rem 0;
    > ul {
      > li {
        display: inline-block;
        + li {
          &:before {
            content: ">";
            margin: 0 .75rem 0 0.5rem; } } } } } }
